<template>
<div id="searchResult">
  <div id="header">
  <input type="text" v-model="query" id="input" v-on:keyup.enter="submit">
  <button v-on:click="submit" id="submit">search</button>
  </div>
  <div id="result"><p id="result_number">{{ tips }}</p>
