	    <p class="item_description">{{ item.description }}</p>
	    <li id="small_url_content"><a v-bind:href="item.url" id="small_url" target="_blank">{{ item.url }}</a></li>
	    <li id="date">&nbsp{{ item.date }}</li>
    </div>
	</div>
