      ]*/
    }
  },
  computed:{
  	length: function(){
  		var len=0
  		for(let item in this.response){
  			len++
  		}
  		return len
  	},
  	part_response: function(){
  		var part=[]
  		for(let start = (this.page_select-1)*10;start<this.page_select*10;start++){
  			if(start<this.length){
  				part.push(this.response[start])
  			}
  		}
  		//shixian guanjianzi gaoliang;
  		var split_query = this.query.split("")
  		console.log(split_query)
  		var char
  		var part_to_str =  JSON.stringify(part)
  		//console.log( typeof part_to_str)
  		/*for(char in split_query){
  			let re =new RegExp(split_query[char],"g"); // re为/^\d+bl$/gim
  			console.log(re)
  			//part_to_str=part_to_str.replace(/18/g,'<span class="hightLight">'+split_query[char]+'</span>')
  			part_to_str=part_to_str.replace(re,"<span color='red'>"+split_query[char]+'</span>')
  		}
  		console.log(part_to_str)
  		part = JSON.parse(part_to_str)*/
  		return part
  	}
  },
  methods: {
  	submit: function(){
  		this.tips='正在查询。。。'
  		var qs = require('qs');
  		//alert(this.query)
  		this.$axios.post('./back_end.php',
  			qs.stringify({
  			'question': this.query
  		})).then(response=>{
  			this.show_button=true
  			this.response=response.data
  			//console.log(response.data)
  			this.tips='为您找到大约'+this.length+'个结果'
  		})
  		.catch(error=>{
  			this.tips='查询失败，请检查网络连接'
  			console.log(error)
  		})
  	}
