</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hightLight{
	color: red;
}
#searchResult{
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	
}
#header{
    margin-top: 0;
    padding: 0;
    width: 100%;
    height: 80px;
    background-color: #FFF;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 1;
}
#input{
	width: 600px;
    height: 35px;
    //border-color:#39F;
    border-style:solid;
    border-width:1px;
    font-size:24px;
    position: relative;
    left: 15%;
    top: 8px;
}
#submit{
	height:40px;
    width:120px;
    background-color:#39F;
    border:none;
    //border-style:solid;
    font-size:24px;
    color:#FFF;
    margin-top: -1px;
    //margin-left:-10px;
    position: relative;
    top: 8px;
    left: 10%;
}
#result{
    height: auto;
    width: 40%;
    margin-top: 80px;
    position: relative;
    left: 15%;
}
#result_number{
	color: gray;
	font-size: 16px;
}
#result a{
    font-size: 18px;
    color: #0000FF;
}
#result p{
    margin:2px;
}
/*#result li{
    display: inline-block;
    font-size: 12px;
    color: #666666;
}*/
#small_url_content{
	display: inline-block;
}
#date{
	display: inline-block;
    font-size: 12px;
    color: #666666;
}
#result li a{
	display:block;/*内联对象需加*/
    width:15em;
    word-break:keep-all;/* 不换行 */
    white-space:nowrap;/* 不换行 */
    overflow:hidden;/* 内容超出宽度时隐藏超出部分的内容 */
    text-overflow:ellipsis;/* 当对象内文本溢出时显示省略标记(...) ；需与overflow:hidden;一起使用。*/
    font-size: 12px;
    text-decoration: none;
    color: #008B00;
}
#footer{
	position: relative;
	left: 15%;
	
}
#footer button{
	margin-right:20px;
	font-size: 15px;
	background-color: #FFF;
	border:solid 1px #FFF;
	padding: 10px 14px 10px 14px;
	//border:none;
	color: #191970;
}
#footer button:hover{
	border:solid 1px #39F;
	cursor: hand;
}
</style>



